<template>
  <div class="table-list-page">
    <div class="search-group">
      <!-- <gs-search
        class="search-table"
        placeholder="请输入表的名称"
        @search="handleSearch"
      /> -->
      <gs-input
        v-model="keyword"
        class="search-name"
        placeholder="请输入表的名称"
      />
      <gs-input
        v-model="tag"
        class="search-tag"
        placeholder="请输入标签的名称"
      />
      <gs-button
        type="primary"
        @click="searchTable"
      >搜索</gs-button>
      <div class="clearfix" />
    </div>
    <div class="content-header">
      <bread-crumb current-level="数据管理" />
    </div>
    <two-column-layout>
      <side-tree
        ref="sideTree"
        slot="left"
        :loading="isLoadingCategory"
        :active="activeCategory"
        :actions="actions"
        :tree-props="treeProps"
        @search="handleCategorySearch"
        @node-click="handleSelectCategory"
        @node-action="handleNodeAction"
        @create-category="editCategory(1)"
      />
      <template slot="right">
        <h3 class="list-title">
          {{ activeCategory.name }}
          <gs-icon
            :title="isRefreshing ? '正在刷新缓存...' : `刷新缓存`"
            :name="isRefreshing ? 'loading' : ` iconfont icon-shuaxin2`"
            :disabled="isRefreshing"
            type="primary"
            mode="button"
            @click="refreshCache"
          />
        </h3>
        <div class="action-group">
          <!-- <gs-button
            type="primary"
            @click="toCntAnalysis"
          >内容解析</gs-button> -->
          <add-relation
            :category-guid="activeCategory.categoryGuid"
            :disabled="isDisabled"
            @create-success="query"
          />
          <action-comfirm
            v-if="!isSearchMode && !isDisabled"
            title="确定批量取消关联吗？"
            @comfirm="handleDeleteRelation"
          >
            <gs-button
              slot="reference"
              :loading="multiDeleting"
              type="primary"
            >批量取消关联</gs-button>
          </action-comfirm>
          <gs-button
            v-if="!isSearchMode && isDisabled"
            type="primary"
            disabled
          >批量取消关联</gs-button>
        </div>

        <data-table
          ref="relationTable"
          :loading="isRelationLoading"
          :table-props="tableProps"
          :pagination-props="paginationProps"
          @selection-change="handleSelectionChange"
          @page-change="handlePageChange"
          @page-size-change="handlePageSizeChange"
        >
          <el-table-column
            v-if="!isSearchMode"
            key="selection"
            type="selection"
            width="55"
          />
          <el-table-column
            key="tableName"
            prop="tableName"
            label="数据表"
          >
            <template slot-scope="scope">
              <a
                :class="{
                  'is-deleted': scope.row.status === 'DELETED'
                }"
                title="查看表详情"
                href="javascript:void(0);"
                @click="jump2table(scope.row)"
              >
                <gs-icon name=" iconfont icon-biaoge" />{{ scope.row.tableName }}
              </a>
            </template>
          </el-table-column>
          <el-table-column
            key="dbName"
            prop="dbName"
            label="所属数据库"
          >
            <template slot-scope="scope">
              <gs-icon name=" iconfont icon-yuanshuju" />{{ scope.row.dbName }}
            </template>
          </el-table-column>
          <el-table-column
            key="path"
            prop="path"
            label="路径"
          />
          <el-table-column
            v-if="!isSearchMode"
            key="op"
            label="操作"
            width="100"
          >
            <template slot-scope="scope">
              <action-comfirm
                v-if="!isDisabled"
                title="确定取消关联吗？"
                @comfirm="handleDeleteRelation(scope.row)"
              >
                <a slot="reference">
                  {{ singleDeleting === scope.row ? '正在取消...' : '取消关联' }}
                </a>
              </action-comfirm>
              <gs-button
                v-else
                type="text-primary"
                disabled
              >取消关联</gs-button>
            </template>
          </el-table-column>
        </data-table>
      </template>
    </two-column-layout>
    <edit-category
      :is-show.sync="visible"
      :type="actionType"
      :category="editingCategory"
    />
  </div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import categoryMixin from './category-mixin';
import ActionComfirm from 'src/components/action-comfirm';
import DataTable from 'src/components/data-table';
import TwoColumnLayout from 'src/components/two-column-layout';
import AddRelation from './add-relation';
import EditCategory from './edit-category';
import BreadCrumb from 'src/components/breadcrumb';
import {
  checkLoading
} from 'src/store/status';
import './style';
// 是否需要显示缓存界面
let keepAlive = false;

export default {
  name: 'DataManagement',
  components: {
    ActionComfirm,
    DataTable,
    TwoColumnLayout,
    AddRelation,
    EditCategory,
    BreadCrumb
  },

  mixins: [categoryMixin],

  data() {
    return {
      keyword: '',
      page: 1,
      pageSize: 10,
      selecteds: [],
      singleDeleting: null,
      multiDeleting: false,
      tag: ''
    };
  },

  computed: {
    ...mapState('relation', {
      currentRelations: 'currentRelations',
      searchRelations: 'searchRelations',
      relationsStatus: 'relationsStatus'
    }),
    ...mapState('cache', {
      dataRefreshStatus: 'dataRefreshStatus'
    }),
    ...mapState('user', {
      modules: 'modules'
    }),

    tableData() {
      const {
        currentRelations,
        searchRelations,
        isSearchMode,
        activeCategory
      } = this;
      let data = [];

      if (isSearchMode) {
        data = searchRelations;
      } else {
        data = currentRelations;
      }

      return (activeCategory.categoryGuid || isSearchMode) ? data : {total: 0, list: []};
    },
    tableProps() {
      const {
        tableData
      } = this;

      return {
        total: tableData.total,
        data: tableData.list
      };
    },
    paginationProps() {
      const total = this.tableProps.total || 0;

      return {
        current: this.page,
        pageSize: this.pageSize,
        total
      };
    },
    isRelationLoading() {
      return checkLoading(this.relationsStatus);
    },
    isRefreshing() {
      return checkLoading(this.dataRefreshStatus);
    },
    isDisabled() {
      return this.activeCategory.status !== 2;
    }
  },

  watch: {
    activeCategory(category) {
      if (category) {
        this.page = 1;
        this.getCategoryRelation();
      }
    },
    tableData(data) {
      // 最后一页没有数据，查询前一页
      if (data.list.length === 0 && data.total !== 0) {
        this.page = Math.ceil(data.total / this.pageSize);
        this.query();
      }
    }
  },

  /**
   * 进入页面触发
   */
  beforeRouteEnter(to, from, next) {
    // 获取当前访问是否需要显示缓存页面
    keepAlive = to.meta.keepAlive;
    next();
  },
  /**
   * 路由跳转前触发
   */
  beforeRouteLeave(to, from, next) {
    // 设置不需要缓存
    from.meta.keepAlive = false;
    next();
  },
  /**
   *keep-alive的钩子函数
   */
  activated() {
    // 判断不需要缓存情况下刷新
    if (keepAlive === false) {
      this.keyword = '';
      this.tag = '';
      this.isSearchMode = false;
      this.activeCategory = {};
      this.getTopLevelCategory();
    }
  },
  mounted() {
    this.getTopLevelCategory();
  },

  methods: {
    ...mapActions('relation', [
      'getRelations',
      'deleteRelation',
      'findRelations'
    ]),
    ...mapActions('cache', [
      'refreshDataCache'
    ]),

    refreshCache() {
      this.refreshDataCache().then(() => {
        this.$Message.success('刷新缓存成功');
      });
    },

    afterDeleteCategory(categoryGuid) {
      if (this.activeCategory.categoryGuid === categoryGuid) {
        this.activeCategory = this.categories.list[0];
      }
    },

    toCntAnalysis() {
      this.$router.push({
        path: '/main/data/analysis'
      });
    },

    getCategoryRelation() {
      const {
        activeCategory
      } = this;
      if (!activeCategory.categoryGuid) return;
      return this.getRelations({
        categoryGuid: activeCategory.categoryGuid,
        param: {
          offset: (this.page - 1) * this.pageSize,
          limit: this.pageSize
        }
      });
    },
    /**
     * 查询页面
     */
    query() {
      if (this.isSearchMode) {
        this.findRelations({
          filterTableName: this.keyword,
          tag: this.tag,
          param: {
            offset: (this.page - 1) * this.pageSize,
            limit: this.pageSize
          }
        });
      } else {
        this.getCategoryRelation();
      }
    },
    handlePageChange(page) {
      this.page = page;
      this.query();
    },

    handlePageSizeChange(pageSize) {
      this.page = 1;
      this.pageSize = pageSize;
      this.query();
    },

    handleSearch(value) {
      this.page = 1;
      this.keyword = value;
      this.isSearchMode = !!value;
      this.query();
    },

    searchTable() {
      const {
        keyword,
        tag
      } = this;
      this.page = 1;
      this.isSearchMode = !!keyword || !!tag;
      this.query();
    },
    /**
     * 关联列表复选框change event
     */
    handleSelectionChange(selecteds) {
      this.selecteds = selecteds;
    },

    handleDeleteRelation(row) {
      let selecteds = this.selecteds;
      let categoryGuid = (row ? row.categoryGuid : null) || this.activeCategory.categoryGuid;

      if (row) {
        if (this.singleDeleting === row) {
          return;
        }
        selecteds = [row];
      }

      let relations = selecteds.map(item => {
        return {
          relationshipGuid: item.relationshipGuid
        };
      });
      let done = () => {
        this.singleDeleting = null;
        this.multiDeleting = false;
      };

      if (relations.length === 0) {
        this.$Message.warning('请选择要取消关联的表');
        return;
      }
      if (row) {
        this.singleDeleting = row;
      } else {
        this.multiDeleting = true;
      }
      this.deleteRelation({
        relations,
        categoryGuid
      }).then(() => {
        done();
        this.selecteds = [];
        this.$Message.success('取消关联成功');
        this.query();
      }, done);
    },

    jump2table(row) {
      const {
        modules
      } = this;
      const hasAuth = modules.modules && modules.modules.some(i => i.moduleId === 7);
      if (!hasAuth) {
        this.$Message.warning('暂无查看表详情的权限，请找管理员开通');
        return;
      }
      this.$router.push({
        path: `/main/metadata/detail/${row.tableGuid}`,
        query: {
          from: 'technical'
        }
      });
    }
  }
};
</script>




